/* eslint-disable camelcase */
const isWebAssemblySupported = typeof WebAssembly !== 'object';

// Built with clang version 11.0.0 (https://github.com/llvm/llvm-project.git 0160ad802e899c2922bc9b29564080c22eb0908c)
// Built from meshoptimizer 0.16
const wasm_base =
  '';
const wasm_simd =
  '';

// Uses bulk-memory and simd extensions
const detector = new Uint8Array([
  0, 97, 115, 109, 1, 0, 0, 0, 1, 4, 1, 96, 0, 0, 3, 3, 2, 0, 0, 5, 3, 1, 0, 1, 12, 1, 0, 10, 22, 2,
  12, 0, 65, 0, 65, 0, 65, 0, 252, 10, 0, 0, 11, 7, 0, 65, 0, 253, 15, 26, 11
]);

// Used to unpack wasm
const wasmpack = new Uint8Array([
  32, 0, 65, 253, 3, 1, 2, 34, 4, 106, 6, 5, 11, 8, 7, 20, 13, 33, 12, 16, 128, 9, 116, 64, 19, 113,
  127, 15, 10, 21, 22, 14, 255, 66, 24, 54, 136, 107, 18, 23, 192, 26, 114, 118, 132, 17, 77, 101,
  130, 144, 27, 87, 131, 44, 45, 74, 156, 154, 70, 167
]);

const FILTERS = {
  // legacy index-based enums for glTF
  0: '',
  1: 'meshopt_decodeFilterOct',
  2: 'meshopt_decodeFilterQuat',
  3: 'meshopt_decodeFilterExp',
  // string-based enums for glTF
  NONE: '',
  OCTAHEDRAL: 'meshopt_decodeFilterOct',
  QUATERNION: 'meshopt_decodeFilterQuat',
  EXPONENTIAL: 'meshopt_decodeFilterExp'
};

const DECODERS = {
  // legacy index-based enums for glTF
  0: 'meshopt_decodeVertexBuffer',
  1: 'meshopt_decodeIndexBuffer',
  2: 'meshopt_decodeIndexSequence',
  // string-based enums for glTF
  ATTRIBUTES: 'meshopt_decodeVertexBuffer',
  TRIANGLES: 'meshopt_decodeIndexBuffer',
  INDICES: 'meshopt_decodeIndexSequence'
};

export function isMeshoptSupported(): boolean {
  return isWebAssemblySupported;
}

export async function meshoptDecodeVertexBuffer(
  target: Uint8Array,
  count: number,
  size: number,
  source: Uint8Array,
  filter: string | number = 'NONE'
): Promise<void> {
  const instance = await loadWasmInstance();
  decode(
    instance,
    instance.exports.meshopt_decodeVertexBuffer,
    target,
    count,
    size,
    source,
    instance.exports[FILTERS[filter]]
  );
}

export async function meshoptDecodeIndexBuffer(
  target: Uint8Array,
  count: number,
  size: number,
  source: Uint8Array
): Promise<void> {
  const instance = await loadWasmInstance();
  decode(instance, instance.exports.meshopt_decodeIndexBuffer, target, count, size, source);
}

export async function meshoptDecodeIndexSequence(
  target: Uint8Array,
  count: number,
  size: number,
  source: Uint8Array
): Promise<void> {
  const instance = await loadWasmInstance();
  decode(instance, instance.exports.meshopt_decodeIndexSequence, target, count, size, source);
}

// eslint-disable-next-line max-params
export async function meshoptDecodeGltfBuffer(
  target: Uint8Array,
  count: number,
  size: number,
  source: Uint8Array,
  mode: string,
  filter: string | number = 'NONE'
): Promise<void> {
  const instance = await loadWasmInstance();
  decode(
    instance,
    instance.exports[DECODERS[mode]],
    target,
    count,
    size,
    source,
    instance.exports[FILTERS[filter || 'NONE']]
  );
}

let wasmPromise: Promise<WebAssembly.Instance>;

async function loadWasmInstance(): Promise<WebAssembly.Instance> {
  // eslint-disable-next-line
  if (!wasmPromise) {
    wasmPromise = loadWasmModule();
  }
  return wasmPromise;
}

async function loadWasmModule(): Promise<WebAssembly.Instance> {
  let wasm = wasm_base;
  if (WebAssembly.validate(detector)) {
    wasm = wasm_simd;

    // eslint-disable-next-line no-console
    console.log('Warning: meshopt_decoder is using experimental SIMD support');
  }

  const result = await WebAssembly.instantiate(unpack(wasm), {});
  await (result.instance.exports as any).__wasm_call_ctors();
  return result.instance;
}

function unpack(data) {
  const result = new Uint8Array(data.length);
  for (let i = 0; i < data.length; ++i) {
    const ch = data.charCodeAt(i);
    result[i] = ch > 96 ? ch - 71 : ch > 64 ? ch - 65 : ch > 47 ? ch + 4 : ch > 46 ? 63 : 62;
  }
  let write = 0;
  for (let i = 0; i < data.length; ++i) {
    result[write++] = result[i] < 60 ? wasmpack[result[i]] : (result[i] - 60) * 64 + result[++i];
  }
  return result.buffer.slice(0, write);
}

// eslint-disable-next-line max-params
function decode(instance, fun, target, count, size, source, filter?) {
  const sbrk = instance.exports.sbrk;
  const count4 = (count + 3) & ~3; // pad for SIMD filter
  const tp = sbrk(count4 * size);
  const sp = sbrk(source.length);
  const heap = new Uint8Array(instance.exports.memory.buffer);
  heap.set(source, sp);
  const res = fun(tp, count, size, sp, source.length);
  if (res === 0 && filter) {
    filter(tp, count4, size);
  }
  target.set(heap.subarray(tp, tp + count * size));
  sbrk(tp - sbrk(0));
  if (res !== 0) {
    throw new Error(`Malformed buffer data: ${res}`);
  }
}
